<template>
	<view class="home">
		<navbar></navbar>
		<tab :list="tabList" @tab='tab'></tab>
		<list-scroll>
			<list-card v-for='iten in 5'></list-card>
		</list-scroll>
	</view>
</template>
<script>
	export default {
		data() {
			return {
				tabList: []
			}
		},
		onLoad() {
			this.getLabel()
		},
		methods: {
			async getLabel() {
				const res = await this.$api.get_label({
					name: 'get_label'
				})
				console.log('ressss', res)
				this.tabList = res.data
			},
			tab(data) {
				console.log('data', data)
			}
		}
	}
</script>

